(function () {
    'use strict';
    var angular = require('angular');
    require('./retrieve.modal/retrieve.modal.component');
    require('./../otp-auth.service');
    require('./otp-verify.component.service');
    require('./otp-verify.component.scss');

    function otpVerify($rootScope, $scope, $state, $window, $timeout, $uibModal, $anchorScroll, UtilService, otpService, otpVerifyService, journeyService, analyticService) {
        var vm = this, otpEnter;
        var navigateData;
        vm.isEmpty = UtilService.isEmpty;
        $rootScope.showSelectedCard = false;
        vm.$onInit = function () {
            $anchorScroll();
            vm.otpRetryCount = 0;
            vm.regenerateOTP = true;
            var count = 0;
            vm.inputs = new Array(6);
            vm.OTPHidden = "";
            vm.cardkey="";
            if ($state.params.otpData) {
                navigateData = {
                    stateData: $state.params
                };
                otpVerifyService.setStateDataInStorage(navigateData);
            }
            if (journeyService.getDataFromStorage())
                vm.stateData = journeyService.getDataFromStorage().stateData;
            if (vm.stateData) {
                if (!vm.stateData.otpData) {
                    vm.mobile = null;
                    vm.dialCode = null;
                    vm.transactionNumber = null;
                    vm.arn = null;
                    vm.txnId = null;
                    vm.signatureConsent = null;
                    $rootScope.stepsHeader.currentStep = 1;
                    analyticService.trackPageLoad(analyticService.pageList.verfication);
                } else {
                    vm.mobile = vm.stateData.otpData.mobile;
                    vm.dialCode = vm.stateData.otpData.dialCode;
                    vm.transactionNumber = vm.stateData.otpData.transactionNumber;
                    vm.arn = vm.stateData.otpData.arn;
                    vm.txnId = vm.stateData.otpData.txnId;
                    if (vm.stateData.otpData.signatureConsent) {
                        $rootScope.stepsHeader.currentStep = 4;
                        vm.signatureConsent = vm.stateData.otpData.signatureConsent;
                        vm.fullName = vm.stateData.otpData.fullName;
                        analyticService.trackPageLoad(analyticService.pageList.esign);
                    } else if (vm.arn) {
                        $rootScope.$broadcast('hide-step-header', {
                            hideHeader: true
                        });
                        analyticService.trackPageLoad(analyticService.pageList.retrieveVerification);
                    } else {
                        analyticService.trackPageLoad(analyticService.pageList.verfication);
                        $rootScope.stepsHeader.currentStep = 1;
                    }
                }
            }
            setFocus();
        };

        vm.$onDestroy = function () {
            navigateData = {
                stateData: null
            };
            otpVerifyService.setStateDataInStorage(navigateData);
        };

        document.body.addEventListener("click", function (event) {
            setFocus();
        });

        document.body.addEventListener("keydown", function (event) {
            // This is to prevent kycValidate() call on enter key press on OTP hidden input
            if (event.target.nodeName === 'INPUT') {
                otpEnter = true;
            } else {
                otpEnter = false;
            }
        });

        var setFocus = function () {
            if ($state.current.name == 'app.journey.otp-verify') {
                var name = $window.document.getElementById('otpTxt');
                if (name) {
                    name.focus();
                }
            }
        }

        vm.delOTP = function (event) {
            if (event.which == 8 || event.which == 46) {
                vm.popOTP();
                // vm.OTPHidden = vm.OTPHidden.slice(0, -1);
            } else if (event.which == 13 || event.keyCode == 13) {
                if (vm.OTPHidden.length === 6) {
                    vm.continue();
                }
                return false;
            } else {
                if (!isNaN(event.key)) {
                    vm.pushOTP(parseInt(event.key));
                    // if (vm.OTPHidden) {
                    //     // var lastone = vm.OTPHidden.toString().split('').pop();
                    //     // vm.input(parseInt(lastone));
                    // }
                }
            }
        };

        vm.pushOTP = function (value) {
            for (var i = 0; i < vm.inputs.length; i++) {
                if (UtilService.isEmpty(vm.inputs[i])) {
                    vm.inputs[i] = value;
                    break;
                }
            }
            vm.OTPHidden = vm.inputs.join('');
        };

        vm.popOTP = function () {
            for (var i = vm.inputs.length - 1; i >= 0; i--) {
                if (!UtilService.isEmpty(vm.inputs[i])) {
                    vm.inputs[i] = '';
                    break;
                }
            }
            vm.OTPHidden = vm.inputs.join('');
        };

        vm.kycValidate = function () {
            if (otpEnter) {
                return;
            }
            var securityParams = {
                robocheck: vm.robocheck ? vm.robocheck : ''
            };
            if (vm.regenerateOTP) {
                vm.regenerateOTP = false;
                var requestParam = {};
                if (vm.signatureConsent) {
                    requestParam.aadhar = parseInt(journeyService.getDataFromStorage().primaryApplicant.aadhaar);
                    requestParam.arn = journeyService.getDataFromStorage().primaryApplicant.arn;
                    otpVerifyService.kycValidate(requestParam, 'mudra', securityParams).then(function (data) {
                        if (data.success) {
                            vm.transactionNumber = data.transactionNumber;
                            clearOTP();
                            $scope.$watch(function () {
                                return vm.regenerateOTP;
                            }, function (newVal, oldVal) {
                                $timeout(function () {
                                    vm.regenerateOTP = true;
                                }, 30000);
                            });
                        }
                    });
                } else {
                    if (vm.arn) {
                        requestParam.pan = journeyService.getDataFromStorage().stateData.otpData.pan;
                        requestParam.dialCode = journeyService.getDataFromStorage().stateData.otpData.dialCode;
                        requestParam.mobile = journeyService.getDataFromStorage().stateData.otpData.mobile;
                        otpVerifyService.retrievalApp(requestParam, securityParams).then(function (data) {
                            if (data.success) {
                                vm.txnId = data.txnId;
                                vm.arn = data.arn;//added this line to get latest arn
                                clearOTP();
                                $scope.$watch(function () {
                                    return vm.regenerateOTP;
                                }, function (newVal, oldVal) {
                                    $timeout(function () {
                                        vm.regenerateOTP = true;
                                    }, 30000);
                                });
                            }
                        });
                    } else {
                        requestParam.aadhar = parseInt(journeyService.getDataFromStorage().primaryApplicant.aadhaar);
                        requestParam.pan = journeyService.getDataFromStorage().primaryApplicant.pan;
                        otpVerifyService.kycValidate(requestParam, 'personal', securityParams).then(function (data) {
                            if (data.success) {
                                vm.transactionNumber = data.transactionNumber;
                                clearOTP();
                                $scope.$watch(function () {
                                    return vm.regenerateOTP;
                                }, function (newVal, oldVal) {
                                    $timeout(function () {
                                        vm.regenerateOTP = true;
                                    }, 30000);
                                });
                            }
                        });
                    }
                }
                analyticService.buttonList.buttonClick.event_content = "Verify OTP: Generate another OTP";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
            }
        };

        var clearOTP = function () {
            vm.OTPHidden = "";
            if (vm.inputs) {
                for (var i = vm.inputs.length - 1; i >= 0; i--) {
                    if (!UtilService.isEmpty(vm.inputs[i])) {
                        vm.inputs[i] = '';
                    }
                }
            }
        };

        var gotoView = function (viewNumber,cardkey) {
           // alert("in view card key" + cardkey);
            switch (viewNumber) {
                // case 0:
                //     $state.go('app.journey.s4-personalDemographic');
                //     break;
                case 1:
                    $state.go('app.journey.s6-incomeDetails',{'cardKey':cardkey});
                    break;
                case 2:
                    $state.go('app.journey.s7-review',{'cardKey':cardkey});
                    break;
                case 3:
                    $state.go('app.journey.card-DIP',{'cardKey':cardkey});
                    break;
                case 4:
                    $state.go('app.journey.balance-transfer',{'cardKey':cardkey});
                    break;
                case 5:
                    $state.go('app.journey.supplementary',{'cardKey':cardkey});
                    break;
                case 6:
                    $state.go('app.journey.document',{'cardKey':cardkey});
                    break;
                case 7:
                    $state.go('app.journey.signature',{'cardKey':cardkey});
                    break;
                case 8:
                    $state.go('app.journey.signature',{'cardKey':cardkey});
                    break;
                case 9:
                    $state.go('app.journey.staff-details',{'cardKey':cardkey});
                    break;
                case 10:
                    $state.go('app.journey.s5-employmentDetails',{'cardKey':cardkey});
                    break;
                case 11:
                    $state.go('app.journey.s3-personalDetails',{'cardKey':cardkey});
                    break;
                default:
                    $state.go('app.journey.s1-cards');
            }
        };

        vm.continue = function () {
            var securityParams = {
                robocheck: vm.robocheck ? vm.robocheck : ''
            };
            console.log("WHAT IS ARN===",vm.arn);
            if (vm.arn) {
                console.log("WHAT IS signatureConsent===",vm.signatureConsent);
                if (vm.signatureConsent) {
                    var requestData = {
                        "aadhar": parseInt(journeyService.getDataFromStorage().primaryApplicant.aadhaar),
                        "otp": vm.inputs.join(''),
                        "otpRetryCount": vm.otpRetryCount,
                        "pan": journeyService.getDataFromStorage().primaryApplicant.pan,
                        "transactionNumber": vm.transactionNumber
                    };
                    otpVerifyService.eMudraData(requestData, vm.arn, securityParams).then(function (data) {
                        if (data.errorCode === 7000 || data.errorCode === 7001 || data.errorCode === 7002) {
                            $state.go('app.complete-ack', {
                                saveData: {
                                    name: vm.fullName,
                                    arn: vm.arn,
                                    statusCode: data.errorCode
                                },cardKey:cardkey
                            });
                        } else if (data.errorCode == 1003) {
                            vm.otpRetryCount = data.otpRetryCount + 1;
                            vm.errMsg = 'Sorry! You seem to have entered invalid OTP. Please try again.';
                            clearOTP();
                            $anchorScroll();
                        } else if (data.errorCode == 4005) {
                            $state.go('app.exception');
                        } else if (data.errorCode == 1015) {
                            $state.go('app.max-otp');
                        } else {
                            vm.errMsg = "Sorry! There seems to be a technical problem. We are unable to progress further. We will contact you within 2 working days to proceed with your Credit Card application.";
                            clearOTP();
                            $anchorScroll();
                        }
                    }, function (err) {
                        vm.errMsg = "Sorry! There seems to be a technical problem. We are unable to progress further. We will contact you within 2 working days to proceed with your Credit Card application.";
                        clearOTP();
                        $anchorScroll();
                    });
                } else {
                    var requestParam = {};
                    requestParam.otp = vm.inputs.join('');
                    requestParam.arn = vm.arn;
                    requestParam.txnId = vm.txnId;
                    requestParam.otpRetryCount = vm.otpRetryCount;
                    otpService.validateRetrieveOTP(requestParam, securityParams).then(function (data) {
                      //  alert("inside validate");
                       //// alert("card key"+data.card.key);
                        vm.cardkey=data.card.key;
                       // alert(vm.cardkey);
                        if (data.errorCode == 1) {
                            otpVerifyService.setRetrievalDataInStorage(data, vm.arn);
                            //arun
                            $rootScope.$broadcast("show-FRN");
                            $scope.$emit('card-selected');
                            if (data.isStaffJourney === "Y") {
                                if (UtilService.isStaffPlatform()) {
                                    gotoView(data.viewNumber,vm.cardkey);
                                } else {
                                    window.location.href = "resources/templates/staff-unsupported-env.html";
                                }
                            } else {
                                gotoView(data.viewNumber,vm.cardkey);
                            }
                        } else if (data.errorCode == 1003) {
                            vm.otpRetryCount = data.otpRetryCount + 1;
                            vm.errMsg = 'Sorry! You seem to have entered invalid OTP. Please try again.';
                            clearOTP();
                            $anchorScroll();
                        } else if (data.errorCode == 1015) {
                            $state.go('app.app-retrieval');
                        } else if (data.errorCode == 1005) {
                            vm.otpRetryCount = data.otpRetryCount + 1;
                            vm.errMsg = 'OTP expired.Please generate new OTP.';
                            clearOTP();
                            $anchorScroll();
                        }else if (data.errorCode == 10006) {
                            //arun else if condition
                           // vm.errMsg = "Sorry! there seems to be some technical error. Please try again";
                            $state.go('app.journey.s1-cards');
                        }
                         else {
                            $state.go('app.app-retrieval');
                        }
                    }, function (err) {
                        vm.errMsg = "Sorry! There seems to be a technical problem. We are unable to progress further. We will contact you within 2 working days to proceed with your Credit Card application.";
                        clearOTP();
                        $anchorScroll();
                    });
                }
            } else {
                $rootScope.$broadcast('trigger-verification-loader', {
                    showLoader: true,
                    showCard: false,
                    loaderType: 'AADHAR'
                });
                vm.showLoader = true;
                var requestParam = {};
                requestParam.aadhar = journeyService.getDataFromStorage().primaryApplicant.aadhaar;
                requestParam.pan = journeyService.getDataFromStorage().primaryApplicant.pan;
                requestParam.otp = vm.inputs.join('');
                requestParam.transactionNumber = vm.transactionNumber;
                requestParam.otpRetryCount = vm.otpRetryCount;
                otpService.validateOTP(requestParam, securityParams).then(function (data) {
                    if (data.errorCode !== 4001) {
                        if (data.errorCode == 2 || data.errorCode == 1007 || data.errorCode == 1015 || data.errorCode == 1016 || data.errorCode == 1017 || data.errorCode == 1018 || data.errorCode == 1201 || data.errorCode == 5007) {
                            hideLoader();
                            if (data.errorCode == 1007) {
                                analyticService.buttonList.error.event_content = "Already a prestige customer";
                                analyticService.trackUserAction(analyticService.buttonList.error);
                            } else if (data.errorCode == 1015) {
                                analyticService.buttonList.error.event_content = "Max OTP attempt exceeded error";
                                analyticService.trackUserAction(analyticService.buttonList.error);
                            } else if (data.errorCode == 1016) {
                                analyticService.buttonList.error.event_content = "Aadhaar/Pan input - max attempt exceeded";
                                analyticService.trackUserAction(analyticService.buttonList.error);
                            } else if (data.errorCode == 1018) {
                                analyticService.buttonList.error.event_content = "Mobile number not registered with Aadhaar";
                                analyticService.trackUserAction(analyticService.buttonList.error);
                            } else if (data.errorCode == 1201) {
                                analyticService.buttonList.error.event_content = "Inelegible age error";
                                analyticService.trackUserAction(analyticService.buttonList.error);
                            } else if (data.errorCode == 2 || data.errorCode == 5007) {
                                analyticService.buttonList.error.event_content = "Connectivity issue error";
                                analyticService.trackUserAction(analyticService.buttonList.error);
                            }
                            $state.go("app.journey.lead-generation", {
                                leadData: {
                                    statusCode: data.errorCode,
                                    fullName: data.fullName,
                                    mobileNumber: data.mobile,
                                    email: data.email,
                                    residenceCity: data.residenceCity,
                                    leadReason: data.leadGenerationReason
                                },cardKey:cardkey
                            });
                        } else if (data.errorCode == 1002 || data.errorCode == 1014) {
                            hideLoader();
                            $state.go('app.journey.s2-qualifying', {
                                step2data: {
                                    errorCode: data.errorCode
                                }
                            });
                        } else if (data.errorCode == 1 || data.errorCode == 1004) {
                            $timeout(function () {
                                otpVerifyService.setDataInStorage(data);
                                $state.go('app.journey.s3-personalDetails', {
                                    stateData: {
                                        errorCode: data.errorCode
                                    },cardKey:cardkey
                                });
                            }, 15000);
                        } else if (data.errorCode == 1011) {
                            hideLoader();
                            //$state.go('app.app-retrieval');
                            var retrieveInstance = $uibModal.open({
                                component: 'retrieveModal'
                            });
                            retrieveInstance.result.then(function (isSelected) {
                                if (isSelected) {
                                    $state.go('app.app-retrieval');
                                }
                            }, function () { });
                        } else if (data.errorCode == 1012 || data.errorCode == 1019 || data.errorCode == 1020) {
                            // Existing pending application
                            hideLoader();
                            analyticService.buttonList.error.event_content = "Existing submitted application error";
                            analyticService.trackUserAction(analyticService.buttonList.error);
                            $state.go('app.submitted-application',{cardKey:cardkey});
                        } else if (data.errorCode == 1003) {
                            hideLoader();
                            vm.otpRetryCount = data.otpRetryCount + 1;
                            vm.errMsg = 'Sorry! You seem to have entered invalid OTP. Please try again.';
                            clearOTP();
                            $anchorScroll();
                        } else if (data.errorCode == 1005) {
                            hideLoader();
                            vm.otpRetryCount = data.otpRetryCount + 1;
                            vm.errMsg = 'OTP expired.Please generate new OTP.';
                            clearOTP();
                            $anchorScroll();
                        } else {
                            hideLoader();
                            vm.errMsg = "Sorry! There seems to be a technical problem. We are unable to progress further. We will contact you within 2 working days to proceed with your Credit Card application.";
                            clearOTP();
                            $anchorScroll();
                        }
                    } else {
                        hideLoader();
                        vm.errMsg = "Sorry! There seems to be a technical problem. We are unable to progress further. We will contact you within 2 working days to proceed with your Credit Card application.";
                        clearOTP();
                        $anchorScroll();
                    }
                }, function (err) {
                    hideLoader();
                    vm.errMsg = "Sorry! There seems to be a technical problem. We are unable to progress further. We will contact you wirhin 2 working days to proceed with your Credit Card application.";
                    clearOTP();
                    $anchorScroll();
                });
            }
            analyticService.buttonList.buttonClick.event_content = "Verify OTP: Continue";
            analyticService.trackUserAction(analyticService.buttonList.buttonClick);
        };

        var hideLoader = function () {
            $rootScope.$broadcast('trigger-verification-loader', {
                showLoader: false,
                showCard: false,
                loaderType: 'AADHAR'
            });
            vm.showLoader = false;
        };

        vm.back = function () {
            journeyService.getDataFromStorage();
            if (vm.arn) {
                if (vm.signatureConsent)
                    $state.go('app.journey.signature');
                else
                    $state.go('app.app-retrieval');
            } else {
                $state.go('app.journey.s2-qualifying');
            }
            analyticService.buttonList.buttonClick.event_content = "OTP Verify: Back";
            analyticService.trackUserAction(analyticService.buttonList.buttonClick);
        };

        vm.closeError = function () {
            vm.errMsg = false;
        };
    }

    var otpVerify = angular.module('app').component('otpVerify', {
        template: require('./otp-verify.component.html'),
        controller: ['$rootScope', '$scope', '$state', '$window', '$timeout', '$uibModal', '$anchorScroll', 'UtilService', 'otpService', 'otpVerifyService', 'journeyService', 'analyticService', otpVerify]
    });

    module.exports = otpVerify.name;
})();


