(function () {
    'use strict';
    var angular = require('angular');
    require('./retrieve.modal.component.scss')

    var modalController = function () {
        var vm = this;

        vm.$onInit = function () {

        };

        /**
         * This function is triggered when we select or unselect card.
         * We push the selected card in selectedPrimaryCards array and
         *  pop the unselected card.
         */
        vm.Close = function (isSelected) {
            vm.close({
                $value: isSelected
            });
        };

        vm.cancel = function () {
            vm.dismiss({
                $value: 'cancel'
            });
        };
    };

    var modalComp = angular.module('app').component('retrieveModal', {
        template: require('./retrieve.modal.component.html'),
        bindings: {
            resolve: '<',
            close: '&',
            dismiss: '&'
        },
        controller: [modalController]
    });
    module.exports = modalComp.name;
})();



