(function () {
    'use strict';
    var angular = require('angular');

    angular.module('app').service('otpService', ['$http', '$q', 'CONFIG', 'appState', '$rootScope', function ($http, $q, CONFIG, appState, $rootScope) {
        var self = this;

        self.generateOTP = function (nric, mobile, email) {

            if (!$rootScope.integrationsActive) {
                return $q.resolve(true);
            }

            var defer = $q.defer();
            $http({
                method: 'POST',
                url: CONFIG.apiUrl + '/otp/',
                headers: {
                    'Content-Type': 'application/json'
                },
                data: {
                    nric: nric,
                    mobile: mobile,
                    email: email
                }
            }).then(function (response) {
                if (response && response.status === 200) {
                    defer.resolve(true);
                } else if (response && response.status === 204) {
                    defer.reject("No application found for the given information");
                } else if (response && response.validationErrors) {
                    defer.reject("Please ensure the values added in form fields are appropriate, and retry.");
                } else {
                    defer.reject("Please ensure the values added in form fields are appropriate, and retry.");
                }
            }, function (response) {
                defer.reject("Some error occured while commnicating with the server. Please ensure the values added in form fields are appropriate, and retry.");
            });
            return defer.promise;
        };

        self.getOTP = function (nric, mobile, email) {

            if (!$rootScope.integrationsActive) {
                return $q.resolve(123456);
            }

            var defer = $q.defer();
            $http({
                method: 'GET',
                url: CONFIG.apiUrl + '/otp/?nric=' + nric + '&mobile=' + mobile + '&email=' + email,
                headers: {
                    'Content-Type': 'application/json'
                }
            }).then(function (response) {
                if (response && response.status === 200) {
                    defer.resolve(response.data);
                } else if (response && response.validationErrors) {
                    defer.reject("Please ensure the values added in form fields are appropriate, and retry.");
                } else {
                    defer.reject("Please ensure the values added in form fields are appropriate, and retry.");
                }
            }, function (response) {
                return defer.reject("Some error occured while commnicating with the server. Please ensure the values added in form fields are appropriate, and retry.");
            });
            return defer.promise;
        };

        self.validateOTP = function (requestParam, params) {
            var apiURL, httpMethod;
            if ($rootScope.integrationsActive) {
                httpMethod = "POST";
                apiURL = CONFIG.apiUrl + '/kyc/otpvalidate' + '?hash_id=' + Math.random();
            } else {
                httpMethod = "GET";
                apiURL = 'resources/data/mock/otp-verify/kyc_otp_validate.json';
            }
            var leadData = appState.getContactDetails();
            var modelData = {
                "aadhar": requestParam.aadhar,
                "otp": requestParam.otp,
                "pan": requestParam.pan.toUpperCase(),
                "transactionNumber": requestParam.transactionNumber,
                "otpRetryCount": requestParam.otpRetryCount,
                "urlParamId": appState.getUrlParamId(),
                "leadId": leadData.leadId
            };
            return $http({
                method: httpMethod,
                url: apiURL,
                data: modelData,
                headers: {
                    'Content-Type': 'application/json'
                },
                params: params,
                hideLoader: true
            }).then(function (response) {
                if (response && response.status === 200) {
                    return response.data;
                } else {
                    //return defer.reject("Please ensure the values added in form fields are appropriate, and retry.");
                }
            }, function (response) {
                return response.data;
            });
        };

        self.validateRetrieveOTP = function (requestParam, params) {
            var apiURL, httpMethod;
            if ($rootScope.integrationsActive) {
                httpMethod = "POST";
                apiURL = CONFIG.apiUrl + '/retrieveapplication/fetchapplication/' + '?hash_id=' + Math.random();
            } else {
                httpMethod = "GET";
                apiURL = 'resources/data/mock/otp-verify/retrieve_fetch_application.json';
            }
            var modelData = {
                "otp": requestParam.otp,
                "arn": requestParam.arn,
                "retryCount": requestParam.otpRetryCount,
                "txnId": requestParam.txnId
            };

            return $http({
                method: httpMethod,
                url: apiURL,
                data: modelData,
                headers: {
                    'Content-Type': 'application/json'
                },
                params: params
            }).then(function (response) {
                if (response && response.status === 200) {
                    return response.data;
                } else {
                    //return defer.reject("Please ensure the values added in form fields are appropriate, and retry.");
                }
            }, function (response) {
                return response.data;
            });
        };
    }]);
})();



